// @Media
$screen-mob: 320px;
$screen-tablet: 768px;
$screen-desktop: 1024px;

// Font-size
$font-regular: 16px;
$font-medium: 24px;
$font-large: 32px;

@font-face {
    font-family: 'Corpid-Medium', sans-serif;
    src: url("../fonts/CorpidE3SCd_500_.woff") format('woff');
    font-style: normal;
    font-weight: 500;
}

@font-face {
    font-family: 'Corpid-Regular', sans-serif;
    src: url("https://assets.flocktory.com/uploads/clients/2679/7e691246-7374-4d72-aad0-81c91c3632fe_CorpidE3SCd_300_.woff");
    font-style: normal;
}

@font-face {
    font-family: 'Corpid-Bold', sans-serif;
    src: url("https://assets.flocktory.com/uploads/clients/2679/1b796607-abb9-4750-9b83-406cd91b4497_CorpidE3SCd_700_.woff");
    font-style: normal;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: Tahoma, Roboto, sans-serif;
}

a {
    text-decoration: none;
}

.hidden-mob {
    display: none;
}

// HEADER

.header {
    display: flex;
    position: relative;
    justify-content: space-between;
    align-items: center;
    padding: 10px 16px;
    background-color: #fff;
    box-shadow: 0 0 6px rgba(110, 119, 130, 0.25);
}

.personal {
    display: flex;
    align-items: center;
}

.personal__link {
    display: block;
    width: 32px;
    height: 32px;
    background-image: url('https://assets.flocktory.com/uploads/clients/2679/30a78d33-6822-4d89-b905-6e621d0d9e52_user.svg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    cursor: pointer;
}

.menu-mobile {
    display: block;
    position: relative;
    border: none;
    outline: none;
    width: 20px;
    height: 2px;
    background-color: #b0b7bf;
    margin-left: 25px;
    cursor: pointer;
}

.menu-mobile::before, .menu-mobile::after {
    content: '';
    display: block;
    position: absolute;
    width: 20px;
    height: 2px;
    background-color: #b0b7bf;
    left: 0;
}

.menu-mobile::before {
    top: -5px;
}

.menu-mobile::after {
    top: 5px;
}

// MAIN

.content {
    background-color: #fff;
}

.banner {
    background-color: #f2f3f7;
    padding: 30px 16px 220px;
    background-image: url('https://assets.flocktory.com/uploads/clients/2679/749e6f3a-cbfe-48be-a7d6-4aeb3163f5e3_hero_banner_image.png');
    background-position: center bottom;
    background-size: 229px 203px;
    background-repeat: no-repeat;
    margin-bottom: 40px;
}

.banner__text {
    font-size: 16px;
    margin: 0 0 10px;
}

.banner__title {
    margin-top: 0;
    font-size: $font-large;
    line-height: 40px;
    text-transform: uppercase;
}

.banner__link {
    display: inline-block;
    padding: 10px 30px;
    color: #fff;
    font-size: $font-regular;
    background: #dd0000;
    border-radius: 5px;
}

.tasks {
    padding: 5px 0;
    margin-bottom: 30px;
}

.section-title {
    font-size: $font-medium;
    line-height: 32px;
    margin-top: 0;
    margin-bottom: 30px;
    padding: 0 16px;
}

.tasks__changes {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 30px;
    padding: 0 16px
}

.task__label {
    width: 100%;
    color: #6e7782;
}

.task__label:not(:last-child) {
    margin-bottom: 10px;
}

.task__checkbox {
    display: none;
    visibility: hidden;
}

.task__text {
    display: block;
    width: 100%;
    border: 1px solid #e2e5eb;
    border-radius: 16px;
    padding: 8px 15px;
    text-align: center;
    font-size: $font-regular;
}

.task__checkbox:checked + .task__label {
    color: #e30611;
}

.cards {
    position: relative;
    min-height: 500px;
    overflow-x: scroll;
}

.cards__item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 30px 25px;
    border-radius: 8px;
    border: 1px solid #e2e5eb;
    min-width: 260px;
}

.cards__wrapper {
    display: flex;
    padding: 0 16px;
    position: absolute;
}

.cards__header, .cards__footer {
    width: 100%;
}

.cards__item:not(:last-child) {
    margin-right: 16px;
}

.cards__title {
    font-size: $font-medium;
    max-width: 200px;
}

.cards__description {
    margin-top: 0;
    font-size: $font-regular;
    line-height: 24px;
}

.cards__bonus {
    font-size: 21px;
    font-weight: 700;
    margin-top: 0;

    span {
        font-size: 34px;
    }

    &--flex {
        display: flex;
        align-items: center;

        b {
            font-size: 45px;
            margin-right: 15px;
        }
    }
}

.cards__link {
    display: block;
    width: 100%;
    background: #E2E5EB;
    border-radius: 5px;
    padding: 8px 15px;
    text-align: center;
    color: #000;
}

.services {
    padding: 5px 16px;
}

.services__title {
    padding: 0;
}

.services__item {
    border: 1px solid #e8e8e8;
    border-radius: 10px;
    padding: 25px 20px;
}

.services__item:not(:last-child) {
    margin-bottom: 20px;
}

.services__vendor {
    font-size: $font-medium;
    line-height: 32px;
    margin-bottom: 10px;
}

.services__text {
    font-size: $font-regular;
    line-height: 24px;
}

@media (min-device-width: $screen-tablet) {

    .header {
        padding: 8px 32px;
    }

    .personal__link {
        width: 40px;
        height: 40px;
    }

    .banner {
        padding: 54px 270px 80px 32px;
        background-position: right 20px bottom;
        background-size: 368px 326px;
        margin-bottom: 45px;
    }

    .banner__text {
        margin: 0 0 20px;
        font-size: 20px;
        font-family: Tahoma, Roboto, sans-serif;
    }

    .banner__title {
        margin-bottom: 60px;
    }

    .banner__link {
        font-size: 21px;
    }

    .section-title {
        text-align: center;
    }

    .tasks__changes {
        flex-direction: row;
        flex-wrap: wrap;
        max-width: 600px;
        margin: 0 auto 30px;
    }

    .task__label {
        width: auto;
        margin-right: 10px;
    }

    .task__label:last-child {
        margin-bottom: 10px;
    }

    .task__label:nth-of-type(3) {
        order: 1;
    }

    .task__label:nth-of-type(3) {
        .task__text {
            padding: 8px 35px;
        }
    }

    .cards__wrapper {
        padding: 0 32px;
    }

    .cards__item {
        padding: 30px;
        width: 276px;
    }

    .services__wrapper {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        flex-wrap: wrap;
    }

    .services__item {
        max-width: 336px;
        display: flex;
        flex-direction: row-reverse;
        align-items: flex-start;
    }
}

@media (min-device-width: $screen-desktop) {
    .hidden-mob {
        display: block;
    }

    .menu-mobile {
        display: none;
    }

    .header {
        padding: 16px 84px;
        justify-content: flex-start;
    }

    .header__services {
        margin-left: 70px;
        color: #000;
    }

    .personal {
        margin-left: auto;
    }

    .personal__link {
        width: auto;
        height: auto;
        padding: 10px 30px;
        color: #fff;
        font-size: $font-regular;
        background: #dd0000;
        border-radius: 5px;
    }

    .logo__title {
        display: inline;
        font-size: 20px;
        color: #e30611;
    }

    .banner {
        padding: 70px 35% 80px 84px;
        background-size: 473px 419px;
    }

    .banner__text {
        font-size: $font-medium;
    }

    .banner__title {
        font-size: 50px;
        line-height: 54px;
        max-width: 650px;
    }

    .tasks__changes {
        flex-direction: row;
        flex-wrap: wrap;
        max-width: 100%;
    }

    .task__label:not(:last-child), .task__label:last-child {
        margin-bottom: 0;
    }

    .task__label:nth-of-type(3) {
        order: 0;
    }

    .task__label {
        margin-right: 12px;
    }

    .cards__wrapper {
        width: 100%;
        justify-content: center;
    }

    .services__item {
        max-width: 47%;
    }

}
